<template>
    <div class="borrowbook"  style="margin:auto">
    <b-container> 
      <el-row :gutter="20">
 <h4></h4>
</el-row>
     <el-row :gutter="20">
  <el-col :span="6">
    <el-select v-model="condition" clearable placeholder="">
      <el-option label="" value=""></el-option>
       <el-option label="" value=""></el-option>
    </el-select>
  </el-col>
  <el-col :span="6">   
    <el-input placeholder="" prefix-icon="el-icon-search" v-model="search">
  </el-input>
  </el-col>
  
  <el-col :span="2">
    
     <el-button type="primary" round size="medium" @click="applysearch" ></el-button>  

  </el-col>
  <el-col :span="2">
    
     <el-button type="primary" round size="medium" @click="reset" ></el-button>  

  </el-col>
   <!-- <el-col :span="80">
    
     <el-button type="primary" round size="medium" @click="addbook"></el-button>  

  </el-col> -->
</el-row>
      <el-table :data="myborrow.slice((currentPage-1)*pagesize,currentPage*pagesize)" border style=" width:100%"  max-height="600">
            <el-table-column prop="userName" label="" width="180"> </el-table-column>
            <el-table-column prop="bookName" label="" width="180"> </el-table-column>
            <el-table-column prop="borrowDate" label="" width="180"> </el-table-column>
            <el-table-column prop="returnDate" label="" width="180"> </el-table-column>
            <el-table-column prop="renewCount" label="" width="180"> </el-table-column>
        <el-table-column label="">
           <template slot-scope="scope">
         <el-button-group v-if="scope.row.returnDate ==null">
     <el-button type="primary" round size="mini" @click="returnBook(scope.row)"></el-button>  
      <el-button type="danger" round  size="mini" @click="renewBook(scope.row)"></el-button>
      
        </el-button-group>
          <el-button-group v-else>
     <el-button type="primary" round size="mini" @click="returnBook(scope.row)"></el-button>  
      
      
        </el-button-group>
           </template> 
      </el-table-column>
        </el-table>

    
     
  <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10]"
                            :page-size="pagesize"        
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="myborrow.length">  
                    </el-pagination>
    
  

    </b-container>
       
    </div>
</template>
<script>
import { exists } from 'fs';
import qs from 'qs';
export default {
          inject:['reload'],
     mounted(){
  
        this.handlehisttory()
  },
     data(){
     return {
          currentPage:1, //
                pagesize:10,    //    
                search:'',
                condition:'',
                myborrow:[],
      }
    },
    methods:{
              // currentPagepagesizedata
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //
        },
        handlehisttory() {
         this.axios.get('borrow/getallborrow'
        
        )   
              .then(res => {

                    if (res.data) {
                      console.log(res.data)  
                      this.myborrow = res.data
                    }
                      else{
                        this.$message.error(``);
                      }
                    
                })
                .catch(err => {
                    this.$message.error(``, 'ERROR!')
                })
        
       

        },
      returnBook(row){
                this.axios
            .post('/borrow/returnBook',qs.stringify({borrowId:row.borrowId,readerId:row.readerId,bookId:row.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 0) {
                    this.$message.success(``)
                    this.reload()
                          }
                          else{
                               this.$message.error(``, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      },

            renewBook(row){
                this.axios
            .post('/borrow/renewBook',qs.stringify({borrowId:row.borrowId,readerId:row.readerId,bookId:row.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 0) {
                    this.$message.success(``)
                    this.reload()
                          }
                          else{
                               this.$message.error(``, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      },
       applysearch(){
          if(this.search =='' || this.condition ==''){
             this.$message.error(``);
             return;
          }
          if(this.condition ==''){
         
               this.axios.post('borrow/selectByUser',qs.stringify({userName:this.search})
        
        )
           
              .then(res => {
            this.bookList = res.data
                    if (res.data) {
                      console.log(res.data)
                      if(res.data){
                        this.myborrow=res.data
                        
                      }
                      else{
                        this.$message.error(``);
                      }
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
          }

           if(this.condition ==''){
               this.axios.post('borrow/selectByBook',qs.stringify({bookName:this.search})
        
        )
           
              .then(res => {
            this.bookList = res.data
                    if (res.data) {
                      console.log(res.data)
                      if(res.data){
                        this.myborrow=res.data
                        
                      }
                      else{
                        this.$message.error(``);
                      }
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
          }
          
          
        },
        addbook(){
            this.$router.push('/bookchange');
        },
        reset(){
          this.onSearch = false;
          this.currentPage = 1;
          this.handlehisttory()
          this.search=''
          this.condition=''
        },
       
        
    }
}
</script>

</script>

<style scoped>
  .borrowbook{
    height: 500px;
  }
   .el-row {
    margin-bottom: 20px;
    margin-top: 20px;
   
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>